// Custom useInterval hook to allow for easier, more straight forward
// use of setInterval
// Credits: https://www.geeksforgeeks.org/reactjs-useinterval-custom-hook/#

import { useEffect, useRef } from "react";

// Creating the custom useInterval hook
export function useInterval(callback, delay) {
  // Creating a ref
  const savedCallback = useRef();

  // To remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Combining the setInterval and
  // clearInterval methods based on delay.
  useEffect(() => {
    function func() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(func, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}
